@import "../../_globalColor";

.main {
  width: 90%;
  padding: 20px 10px;
  margin: 0px auto;
}

.about-main {
  width: 90%;
  padding: 20px 10px;
  margin: 0px auto;
}

.aboutme-main {
  display: flex;
}

.subTitle {
  color: $subTitle !important;
}

.aboutme-main {
  display: flex;
}

.main-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

.aboutme-main>* {
  flex: 1;
  margin-bottom: 30px;
}

.aboutme-text {
  font-size: 70px;
  line-height: 1.1;
  color: $subTitle;
}

.aboutme-text-p {
  font-size: 30px;
  line-height: 40px;
  width: 80%;
  text-align: left;
}

.wave-emoji {
  animation-duration: 1.8s;
  animation-iteration-count: infinite;
  animation-name: wave;
  display: inline-block;
  transform-origin: 70% 70%;
}

@keyframes wave {
  0% {
    transform: rotate(0deg);
  }

  10% {
    transform: rotate(-10deg);
  }

  20% {
    transform: rotate(12deg);
  }

  30% {
    transform: rotate(-10deg);
  }

  40% {
    transform: rotate(9deg);
  }

  50% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

.techstack-text-div {
  display: flex;
  justify-content: center;
  gap: 25%;
}

.techstack-text {
  font-size: 50px;
  line-height: 1.1;
  color: $textColor;
  text-align: center;
}

.techstack-subtext {
  font-size: 40px;
  line-height: 40px;
}

.techstack-text-p {
  font-size: 30px;
  line-height: 40px;
}

/* Media Query */
@media (max-width: 1380px) {
  .aboutme-text {
    font-size: 50px;
  }

  .aboutme-text-p {
    font-size: 20px;
  }

  .techstack-text {
    font-size: 50px;
  }

  .techstack-text-p {
    font-size: 20px;
  }
}

@media (max-width: 768px) {
  .aboutme-text {
    font-size: 30px;
    text-align: center;
  }

  .aboutme-text-p {
    font-size: 16px;
    line-height: normal;
  }

  .aboutme-main {
    display: block;
  }

  .techstack-text-div {
    display: block;
    justify-content: center;
    gap: 25%;
  }

  .techstack-text {
    font-size: 30px;
  }

  .techstack-subtext {
    font-size: 20px;
    text-align: left;
  }

  .techstack-text-p {
    font-size: 16px;
    line-height: normal;
    text-align: left;
  }
}

@media (max-width: 320px) {
  .main {
    width: auto;
  }
}